.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.footer{
  position: relative;
  height: 10%;
  background-color: #d2d3d5;
  display: flex;
  justify-content: end;
  gap:10px;
  padding: 0px 20px;
  align-items: center;
  button{
      height: 60%;
      padding: 5px 10px;
      border-radius:20px ;
      border: 2px solid black;
  }
  

}
